import React from 'react'
import Domino from './Domino';
import EmilPost from "./../images/Emil_Leon_Post.jpg"

export default function About() {
    return (
        <div id="about">
            <img src={EmilPost} alt="Emil Leon Post" id='emil'/>
            <h2>
                Le probleme de correspondance de Post (PCP pour les intimes)
                est un problme imagin en 1946 par Emile Post.
                Il sagit dun problme indcidable dans lequel
                il faut trouver une suite de dominos  mettre les uns
                 cots des autres afin que la partie suprieur des dominos
                forment le mme mot que la partie infrieure
                (tout cela sans les faire tourner).
            </h2>
            <h2>
                Par exemple:
                On a les dominos suivant:
            </h2>
            <div id="game">
                <Domino topText="1" bottomText="111"></Domino>
                <Domino topText="10111" bottomText="10"></Domino>
                <Domino topText="10" bottomText="0"></Domino>
            </div>
            <h2>
                Par exemple:
                On a les dominos suivant:
            </h2>
            <div id="game">
                <Domino topText="10111" bottomText="10"></Domino>
                <Domino topText="1" bottomText="111"></Domino>
                <Domino topText="1" bottomText="111"></Domino>
                <Domino topText="10" bottomText="0"></Domino>
            </div>
            <h2>Indcidabilit:</h2>
            <h2>
                On a vu avec lexemple ci-dessus que rsoudre une instance de PCP est fort complexe,
                mme si celui-ci est de petite taille.
                Cest parce que ce problme est indcidable.
                Cela veut dire quon ne peut pas prdire la taille de la solution
                en se basant sur les dominos donns, en effet certaines instances de
                tailles comparables  celle ci-dessus ont une solution dune longueur de 76 dominos!
            </h2>
            <div id='game'>
            </div>
        </div>
    )
}
